<template>
    <div :v-if="catefind">
        <p-header>{{catefind.cname}}</p-header>
        <van-pull-refresh v-model="isLoading" @refresh="getgoods">
            <div class="box" v-if="catefind">
                <div class="banner" v-if="catefind.thumb">
                    <img :src="imgpath+catefind.thumb" :alt="catefind.cname">
                </div>
                <div class="top" :v-if="goodsall" v-for="item in goodsall" :key="item.id">
                    <router-link to="{name:'details'},query:{id:item.id}}">
                        <div class="toptupian">
                            <img :src="imgpath+item.thumb" :alt="item.gname">
                            <div class="yisongyi"></div>
                        </div>
                        <div class="hezi">
                            <div class="zi">{{item.gname}}</div>
                            <span class="qianshu">{{item.sale}}</span>
                            <span class="qwe">{{item.mprice}}</span>
                            <div class="goumai">立即购买</div>
                        </div>
                    </router-link>
                </div>
                <!--<div class="title">活动规则</div>-->
                <!--<div class="title1">-->
                <!--<p>1.秒杀商品同一设备同一用户仅有1次抢购机会，不及时支付订单导致订单取消等于放弃抢购，机会有限先到先得</p>-->
                <!--<p>2.商品数量有限，售完即止。需及时在线支付，请以订单支付成功时间为准；</p>-->
                <!--<p>3.若发现用户恶意刷单，爱鲜蜂有权拒绝送货。</p>-->
                <!--</div>-->
                <!--<div class="bottom">-->
                <!--<div class="bottom-left">-->
                <!--<p>1</p>-->
                <!--</div>-->
                <!--<div class="text">￥10</div>-->
                <!--<a href="">-->
                <!--<div class="huang">去结算</div>-->
                <!--</a>-->
                <!--</div>-->
            </div>
        </van-pull-refresh>
    </div>
</template>

<script>
    import PHeader from '../../components/p-header/p-header';
    import axios from 'axios';
    import {HOSTNAME, IMGPATH} from "../../config/base";

    export default {
        name: "List",
        data() {
            return {
                id: 0,
                isLoading: false,
                catefind: [],
                goodsall: [],
                imgpath: IMGPATH,
                limit: 3,
                current: 0,
                page: 1
            }
        },
        methods: {
            getcate() {
                axios.get(HOSTNAME + '/api/cate/' + this.id)
                    .then(res => {
                        this.catefind = res.data.data;
                    })
                    .catch(error => console.log(error))
            },
            getgoods() {
                this.current++;
                if (this.current > 1 && this.current > this.page) {
                    this.isLoading = false;
                    return;
                }
                axios.get(HOSTNAME + '/api/categoods/' + this.id, {
                    params: {
                        'current': this.current,
                        'limit': this.limit
                    }
                })
                    .then(res => {
                        this.goodsall.push(...res.data.goods);
                        this.page = Math.ceil(res.data.count / this.limit);
                        this.$nextTick(() => {
                            this.isLoading = false;
                        });
                    })
            }
        },
        components: {
            PHeader
        },
        beforeMount() {
            this.id = this.$route.query.id;
            this.getcate();
            this.getgoods();
        }
    }
</script>

<style scoped>
    @import url("../../style/mr.css");
</style>